$background_main: #FbFbFb;
html, body {
  height: 100%;
  font-family: 'Roboto', sans-serif; }
body {
  padding-top: 64px;
  background: $background_main; }
#header-main {
  height: 64px;
  background: $background_main;
  box-shadow: 0 0 5px 1px rgba(64, 63, 63, 0.4);
  .container {
    padding-top: 5px;
    padding-bottom: 4px;
    div {
      max-height: 55px;
      max-width: 245px; } } }
main {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  #display-map {
    position: fixed;
    left: 40%;
    width: 60%;
    height: calc(100% - 64px);
    border-left: 2px solid rgba(64, 63, 63, 0.4);
    #map {
      height: 100%; } }
  #details-list {
    width: 40%;
    height: 100%;
    background: $background_main;
    #more-info {
      display: none; }
    nav {
      padding: 40px 30px 50px 60px; } } }


@media all and (min-width: 1400px ) {
  main {
    #display-map {
      left: 35%;
      width: 65%; }
    #details-list {
      width: 35%; } } }

@media all and (max-width: 1000px ) {
  main {
    #details-list {
      nav {
        padding-left: 30px; } } } }

@media all and (max-width: 800px ) {
  main {
    #details-list {
      nav {
        padding-left: 15px;
        padding-bottom: 15px; } } } }

@media all and (max-width: 650px ) {
  main {
    flex-direction: column;
    flex-wrap: wrap;
    #display-map {
      left: 0%;
      width: 100%;
      height: calc(100% - 115px);
      border: none; }
    #details-list {
      width: 100%;
      height: auto;
      position: absolute;
      z-index: 300;
      #more-info {
        display: block;
        width: 100%;
        background: #0084FF;
        height: 51px;
        border-top: 2px solid rgba(64, 63, 63, 0.4);
        padding: 5px 20px;
        color: white;
        text-align: center;
        z-index: 300;
        button {
          width: 90%;
          background: $background_main;
          border: 2px solid $background_main;
          color: black;
          padding: 6px 15px;
          font-weight: 600;
          cursor: pointer;
          &:hover {
            background: #0084FF;
            color: white; } } }
      &.bottom-50 {
        top: calc(100% - 51px); }
      &.top-reset {
        top: 0;
        min-height: calc(100% - 51px);
        #more-info {
          position: fixed; } } }
    nav {
      margin-top: 51px;
      #stations-details {
        min-height: 100%; } }
    nav.hidden-info {
      display: none; }
    nav.show-info {
      display: block; } } }

.glyphicon.glyphicon-map-marker {
  font-size: 26px; }

#bikes-availability {
  div {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center; } }

.details-style {
  font-weight: bold;
  .well {
    background: #31B0D5;
    color: white; }
  li {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center; } }

